.field-wrap {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin: 50px auto; }

.field {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    &__row {
        display: flex;
        flex-wrap: wrap;
        // &:last-child
        //     .field__item
 }        //         border-bottom: 2px solid #DFE4E8

    &__item {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 51px;
        height: 51px;
        // border: 2px solid #DFE4E8
        // border: 1px solid red
        cursor: pointer;
        border-left: none;
        border-bottom: none;
        font-family: sans-serif;
        font-size: 20px;
        font-weight: 600;
        background-color: #fff;
        // &:first-child
        //     border-left: 2px solid #DFE4E8

        .borders {
            position: absolute;
            width: calc(100% + 2px);
            height: calc(100% + 2px);
            top: -1px;
            left: -1px;
            right: -1px;
            bottom: -1px;
            // box-shadow: inset 0 0 0 2px red
            // border: 2px solid #DFE4E8

            &__border {
                position: absolute;
                background-color: #000;
                z-index: 50;
                &--top {
                    width: 100%;
                    height: 2px;
                    top: 0;
                    left: 0; }
                &--right {
                    width: 2px;
                    height: 100%;
                    top: 0;
                    right: 0; }
                &--bottom {
                    width: 100%;
                    height: 2px;
                    left: 0;
                    bottom: 0; }
                &--left {
                    width: 2px;
                    height: 100%;
                    top: 0;
                    left: 0; } } }


        &--active {
            background: green;
            .borders {
                &__border {
                    background: red;
                    z-index: 100; } } }

        &--empty {
            font-size: 0;
            background-color: gray;
            .borders {
                &__border {
                    background: gray;
                    z-index: 40; } } }

        &.rm-top {
            .borders__border {
                &--top {
                    z-index: 50;
                    background-color: green; } } }
        &.rm-bottom {
            .borders__border {
                &--bottom {
                    z-index: 50;
                    background-color: green; } } }
        &.rm-left {
            .borders__border {
                &--left {
                    z-index: 50;
                    background-color: green; } } }
        &.rm-right {
            .borders__border {
                &--right {
                    z-index: 50;
                    background-color: green; } } } } }

